<template>
    <div id="account">
        <form v-if="title === 'signin'" @submit.prevent="login">
            <div class="form-group">
                <label for="username">계정명</label>
                <input type="text" v-model="username" class="form-control">
            </div>
            <div class="form-group">
                <label for="password">Password</label>
                <input type="password" v-model="password" class="form-control">
            </div>
            <button type="submit" class="btn btn-primary">로그인</button>
            <Button body="계정 생성" to="/signup"></Button>
        </form>
        <form v-else @submit.prevent="signup">
            <div class="form-group">
                <label for="username">계정명</label>
                <input type="text" v-model="username" class="form-control">
            </div>
            <div class="form-group">
                <label for="email">메일 주소</label>
                <input type="text" v-model="email" class="form-control">
            </div>
            <div class="form-group">
                <label for="password">Password</label>
                <input type="password" v-model="password" class="form-control">
            </div>
            <button type="submit" class="btn btn-primary">생성</button>
        </form>
    </div>
</template>

<script>
import Button from './reusable/button';

export default {
    components: {
        Button
    },
    props: {
        title: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            username: "",
            password: "",
            email: "",
            message: ""
        }
    },
    computed: {
        loggedIn () {
            return this.$store.state.loggedIn;
        }
    },
    mounted() {
        if (this.loggedIn) {
            this.$router.push("/");
        }
    },
    methods: {
        login () {
            console.log("login");
            if (this.username && this.password) {
                this.$store.dispatch('login', {
                    "username": this.username,
                    "password": this.password
                }).then(() => this.redirect()).catch(error => {
                    this.message = error.message;
                });
            }
        },
        signup() {
            if (this.username && this.password && this.email) {
                this.$store.dispatch('register', {
                    "username": this.username,
                    "password": this.password,
                    "email": this.email
                }).then(() => this.$router.push('/')).catch(error => {
                    this.message = error.message;
                    console.error(error);
                });
            }
        },
        redirect () {
            const { search } = window.location;
            if (search === "") return this.$router.push("/");
            const tokens = search.replace(/^\?/, "").split("&");
            const { returnPath } = tokens.reduce((qs, tkn) => {
                const pair = tkn.split("=");
                qs[pair[0]] = decodeURIComponent(pair[1]);
                return qs;
            }, {});

            // 리다이렉트 처리
            return this.$router.push(returnPath);
        }
    }
}
</script>

<style scoped>
#account {
    height: 100%;
}
</style>